import { DatePicker } from "@mui/x-date-pickers";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers";
import { DemoContainer } from "@mui/x-date-pickers/internals/demo";

function MyDatePicker({ widthSize, labelText, value, setValue }) {


  const handleChange = (date) => {
    setValue(date);
  }

  return (
    <section className={widthSize}>
      <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DemoContainer
          components={["DatePicker"]}
          sx={{ fontFamily: "Poppins" }}
        >
          <DatePicker
            label={labelText}
            onChange={handleChange}
            value={value}
            sx={{
              '&.Mui-focused': { color: '#fff' },
              "& .MuiInputLabel-root": { color: "#fff" }, //styles the label
              "& .MuiInputBase-input": { color: "#fff", fontFamily: 'Poppins, sans-serif' }, //styles the text
              "& .MuiSvgIcon-root": { color: "gray" }, // styles the icon
              width: '100%',
              backgroundColor: '#1D1C20', 
            }}
          />
        </DemoContainer>
      </LocalizationProvider>
    </section>
  );
}

export default MyDatePicker;
