import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope, faLock } from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import MyButton from "./MyButton";
import { apiUrl } from '../utils/proxy'
import { useContext, useState } from "react";
import AuthContext from "../context/AuthContext";

function InputFieldLogin({ handleAccount }) {

  const { login } = useContext(AuthContext);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleEmailChange = (event) => {
    setEmail(event.target.value)
  }
  const handlePasswordChange = (event) => {
    setPassword(event.target.value)
  }

  const handleLogin = async () => {
    try {
      const response = await fetch(`${apiUrl}/api/login/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({email: email, password: password})
      });

      if (!response.ok) {
        throw new Error('Error Fetching Data')
      }
      const data = await response.json()
      // console.log('Login Success', data);
      login(data);
      navigate('/homepage')
    }catch(error){
      console.error('An error Occured: ', error.message);
    }
    
    

  }

  const navigate = useNavigate();



  return (
    <section className="flex flex-col w-full ">

      <form className="w-full flex flex-col min-h-[200px] gap-6">

        <div className="flex">
          <div className="w-[8%] bg-semiDarkColor flex items-center justify-center rounded-l-md">
            <FontAwesomeIcon
              className="text-xl"
              icon={faEnvelope}
              style={{ color: "#808080" }}
            />
          </div>
          <input
            className="w-[70%] h-12 bg-semiDarkColor rounded-r-md outline-none"
            type="text"
            placeholder="Enter Email or Student Number"
            value={email}
            onChange={handleEmailChange}
          />
        </div>

        <div className="flex">
          <div className="w-[8%] bg-semiDarkColor flex items-center justify-center rounded-l-md">
            <FontAwesomeIcon
              className="text-xl"
              icon={faLock}
              style={{ color: "#808080" }}
            />
          </div>
          <input
            className="w-[70%] h-12 bg-semiDarkColor rounded-r-md outline-none"
            type="password"
            placeholder="Enter Password"
            value={password}
            onChange={handlePasswordChange}
          />
        </div>

        <MyButton 
          btnText={'Login'}
          btnHeight={'h-9'}
          btnWidth={'w-[78%]'}
          handleClick={handleLogin}
        />

        <p className="mt-6 w-[78%] flex justify-center">
        New Student? &nbsp;<span className="cursor-pointer hover:underline" onClick={handleAccount}>Create account</span>.
        </p>
        
      </form>

    </section>
  );
}

export default InputFieldLogin;
