import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeftLong,
  faCircleUser,
} from "@fortawesome/free-solid-svg-icons";
import { useNavigate } from "react-router-dom";
import { useState, useContext, useEffect } from "react";
import MyTextField from "../components/MyTextField";
import infortalLogo from "../assets/images/infortal-logo-white-cropped.png";
import MyButton from "../components/MyButton";
import AuthContext from "../context/AuthContext";
import { apiUrl } from "../utils/proxy";

function ProfilePage() {

  const { userData, refreshUserData } = useContext(AuthContext);

  const [ isDisabled, setIsDisabled ] = useState(true);

  const [ isLoading, setIsLoading ] = useState(false);

  const [ fname, setFname ] = useState("");

  const [ mname, setMname ] = useState("");

  const [ lname, setLname ] = useState("");

  const navigate = useNavigate();

  async function updateStudent() {
    try {
      setIsLoading(true);
      const updatedData = {
        first_name: fname,
        middle_name: mname,
        last_name: lname,
      };
      const studentId = userData.id
      const response = await fetch(`${apiUrl}/api/students/${studentId}/`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          // Add any additional headers if needed
        },
        body: JSON.stringify(updatedData),
      });
  
      if (!response.ok) {
        setIsLoading(false);
        throw new Error(`HTTP error! Status: ${response.status}`);
      }
  
      const data = await response.json();
      if (data) setIsLoading(false)
      setIsDisabled(true)
      refreshUserData();
      // console.log('Student updated successfully:', data);
    } catch (error) {
      console.error('Error updating student:', error);
    }
  }


  useEffect(() => {
    if (userData) {
      setFname( userData.first_name );
      setMname( userData.middle_name);
      setLname( userData.last_name );
    }
  }, [userData]);

  return (
    <main className="h-screen w-full flex bg-dirtyWhite">
      <section className="h-full w-[200px] bg-myDarkColor">
        <section className="w-full flex justify-center py-1">
          <img src={infortalLogo} alt="infortal logo" className="w-[80%]" />
        </section>
      </section>

      <div className="flex flex-col w-full h-full">
        <header className="w-full h-[3.2rem] px-6 py-4 bg-white text-myDarkColor flex items-center justify-center relative">
          <div
            className="absolute left-6 py-1 px-3 bg-transparent flex items-center rounded-md hover:bg-myBlue"
            onClick={() => navigate("/homepage")}
          >
            <FontAwesomeIcon
              className="text-xl text-black"
              icon={faArrowLeftLong}
            />
          </div>
          <p className="text-2xl">View Profile</p>
        </header>

        <div className="flex-1 p-3">
          {userData ? (
            <section className="w-full h-full bg-white relative p-4 overflow-hidden">
            <article className="flex ">
              <FontAwesomeIcon
                className="text-9xl text-myDarkColor pr-4"
                icon={faCircleUser}
              />
              <div className="w-fit p-2">
                <p className="text-center py-3 text-2xl font-medium">
                  {`${userData.first_name} ${userData.middle_name} ${userData.last_name}`}
                </p>
                <p className="text-slate-400">#{userData.student_number}</p>
              </div>
            </article>
            <section className="px-4 mt-7 flex gap-8 flex-wrap">
              <MyTextField
                labelText={"Firstname"}
                widthSize={"w-[25%]"}
                paddingTop={"pt-[8px]"}
                type={"str"}
                value={fname}
                setValue={setFname}
                bgColor={"#fff"}
                textColor={"black"}
                placeholderColor={"#9CA3AF"}
                isDisabled={isDisabled}
              />

              <MyTextField
                labelText={"Middlename"}
                widthSize={"w-[25%]"}
                paddingTop={"pt-[8px]"}
                type={"str"}
                value={mname}
                setValue={setMname}
                bgColor={"#fff"}
                textColor={"black"}
                placeholderColor={"#9CA3AF"}
                isDisabled={isDisabled}

              />

              <MyTextField
                labelText={"Lastname"}
                widthSize={"w-[25%]"}
                paddingTop={"pt-[8px]"}
                type={"str"}
                value={lname}
                setValue={setLname}
                bgColor={"#fff"}
                textColor={"black"}
                placeholderColor={"#9CA3AF"}
                isDisabled={isDisabled}

              />
            </section>
    
            <section className="w-full h-fit absolute bottom-5 flex gap-9">
              <MyButton
                btnText={ isDisabled ? "Edit" : 'Cancel' }
                btnHeight={"h-9"}
                btnWidth={"w-[100px]"}
                handleClick={() => setIsDisabled(!isDisabled)}
              />
              
              <MyButton
                btnText={"Save"}
                btnHeight={"h-9"}
                btnWidth={"w-[100px]"}
                handleClick={() => updateStudent()}
                isLoading={isLoading}
              />
            </section>
          </section>

          ) : (
            <section className="w-full h-full bg-white relative p-4 overflow-hidden">
              <p>Fetching Data...</p>
            </section>
          )}
          
        </div>

      </div>
    </main>
  );
}

export default ProfilePage;
