import { useNavigate } from "react-router-dom";
import { useContext } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleUser, faBars } from "@fortawesome/free-solid-svg-icons";
import AuthContext from "../context/AuthContext";

function Header({ toggleMenu }) {
  const { userData } = useContext(AuthContext);

  const navigate = useNavigate();
  
  return (
    <div className="w-full h-[3.2rem] flex items-center justify-between bg-white px-5">
      <div className="cursor-pointer" onClick={toggleMenu}>
        <FontAwesomeIcon className="header-icon" icon={faBars} />
      </div>
      {/* user info section */}
      <section className="flex items-center">
        {/* User Name Here */}
        <p className="pr-3">{userData ? `${userData.first_name} ${userData.middle_name} ${userData.last_name}` : "------"}</p>

        <FontAwesomeIcon
          className="text-4xl text-myDarkColor"
          icon={faCircleUser}
          onClick={() => navigate("/profile")}
        />
      </section>
    </div>
  );
}

export default Header;
