import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHouse,
  faBookOpenReader,
  faWallet,
  faBook,
  faGraduationCap,
  faUsers,
  faRightFromBracket,
} from "@fortawesome/free-solid-svg-icons";
import infortalLogo from "../assets/images/infortal-icon.png";
import HomePanel from "./Panel/HomePanel";
import PayFeesPanel from "./Panel/PayFeesPanel";
import SubjectPanel from "./Panel/SubjectPanel";
import GradesPanel from "./Panel/GradesPanel";
import AboutUsPanel from "./Panel/AboutUsPanel";
import AuthContext from "../context/AuthContext";
import { useContext, useState } from "react";
function SideMenu({
  toggleMenu,
  sideMenu,
  isToggled,
  handleMenuCLick,
  showSubmenu,
  setShowSubmenu,
}) {
  const { logout } = useContext(AuthContext);

  const [activeComponent, setActiveComponent] = useState("home");

  const handleComponentClick = (componentName) => {
    setActiveComponent(componentName);
  };
  const handleCourseClick = () => {
    if (isToggled === false) {
      toggleMenu();
    }
    setShowSubmenu(!showSubmenu);
  };

  const additionalHomeClass = activeComponent === "home" ? "bg-myBlue" : "";
  const additionalCourseClass = activeComponent === "course" ? "bg-myBlue" : "";
  const additionalPayFeeClass = activeComponent === "payfee" ? "bg-myBlue" : "";
  const additionalAboutClass = activeComponent === "about" ? "bg-myBlue" : "";

  return (
    <div
      className="w-[66px] h-full bg-myDarkColor text-white transition-all relative"
      ref={sideMenu}
    >
      {/* -------------LOGO HERE------------- */}
      <section className="w-full text-center p-4 mb-6">
        <img src={infortalLogo} alt="infortal logo" className="w-[34px]" />
      </section>

      {/* -------------SIDE MENU------------- */}
      <ul className="flex flex-col gap-6 px-2">
        {/* -------------HOME------------- */}
        <li
          className={`w-full h-11 flex items-center cursor-pointer rounded-[8px] hover:bg-myBlue ${additionalHomeClass}`}
          onClick={() => {
            handleMenuCLick(<HomePanel />);
            handleComponentClick("home");
          }}
        >
          <span className="min-w-[50px] flex items-center justify-center">
            <FontAwesomeIcon
              className="w-fit text-2xl text-white"
              icon={faHouse}
            />
          </span>
          {isToggled && <p className="whitespace-nowrap">Home</p>}
        </li>

        {/* -------------COURSE------------- */}
        <li
          className={`w-full h-11 flex items-center cursor-pointer rounded-[8px] hover:bg-myBlue ${additionalCourseClass}`}
          onClick={() => {
            handleCourseClick()
            handleComponentClick("course")
          }}
        >
          <span className="min-w-[50px] flex items-center justify-center">
            <FontAwesomeIcon
              className="w-fit text-2xl text-white"
              icon={faBookOpenReader}
              style={{ color: "#ffffff" }}
            />
          </span>
          {isToggled && <p className="whitespace-nowrap">Course</p>}
        </li>
        {showSubmenu && (
          // -------------SUB MENU------------- //

          <ul className=" flex flex-col gap-6 ml-10">
            {/* -------------SUBJECTS------------- */}
            <li
              className="flex items-center cursor-pointer"
              onClick={() => handleMenuCLick(<SubjectPanel />)}
            >
              <span className="min-w-[50px] flex items-center justify-center">
                <FontAwesomeIcon
                  className="w-fit text-2xl text-white"
                  icon={faBook}
                  style={{ color: "#ffffff" }}
                />
              </span>
              <p>Subjects</p>
            </li>
            {/* -------------GRADES------------- */}
            <li
              className="flex items-center cursor-pointer"
              onClick={() => handleMenuCLick(<GradesPanel />)}
            >
              <span className="min-w-[50px] flex items-center justify-center">
                <FontAwesomeIcon
                  className="w-fit text-2xl text-white"
                  icon={faGraduationCap}
                  style={{ color: "#ffffff" }}
                />
              </span>
              <p>Grades</p>
            </li>
          </ul>
        )}

        {/* -------------PAY FEES------------- */}
        <li
          className={`w-full h-11 flex items-center cursor-pointer rounded-[8px] hover:bg-myBlue ${additionalPayFeeClass}`}
          onClick={() => {
            handleMenuCLick(<PayFeesPanel />);
            handleComponentClick("payfee");
          }}
        >
          <span className="min-w-[50px] flex items-center justify-center">
            <FontAwesomeIcon
              className="w-fit text-2xl text-white"
              icon={faWallet}
              style={{ color: "#ffffff" }}
            />
          </span>
          {isToggled && <p className="whitespace-nowrap">Pay Fees</p>}
        </li>

        {/* -------------ABOUT US------------- */}
        <li
          className={`w-full h-11 flex items-center cursor-pointer rounded-[8px] hover:bg-myBlue ${additionalAboutClass}`}
          onClick={() => {
            handleMenuCLick(<AboutUsPanel />);
            handleComponentClick("about");
          }}
        >
          <span className="min-w-[50px] flex items-center justify-center">
            <FontAwesomeIcon
              className="w-fit text-2xl text-white"
              icon={faUsers}
              style={{ color: "#ffffff" }}
            />
          </span>
          {isToggled && <p className="whitespace-nowrap">About Us</p>}
        </li>
      </ul>

      <section className=" flex flex-col gap-6 px-2 absolute bottom-5 w-full">
        <div
          className="w-full h-11 flex items-center cursor-pointer rounded-[8px] hover:bg-red-700"
          onClick={logout}
        >
          <span className="min-w-[50px] flex items-center justify-center">
            <FontAwesomeIcon
              className="w-fit text-2xl text-white"
              icon={faRightFromBracket}
              style={{ color: "#ffffff" }}
            />
          </span>
          {isToggled && <p className="whitespace-nowrap">Logout</p>}
        </div>
      </section>
    </div>
  );
}

export default SideMenu;
