import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";

function MySelect({ labelText, optionArray, widthSize, paddingTop, selectedOption, setSelectedOption }) {

  const mappedOption = optionArray.map((option) => (
    <MenuItem key={option[0]} value={option[0]}>{option[1]}</MenuItem>
  ));

  const handleChange = (event) => {
    setSelectedOption(() => event.target.value)
  }
  
  return (
    <section className={`${widthSize} + ${paddingTop}` }>
      <Box sx={{ minWidth: "100%" }}>
        <FormControl
          fullWidth
          sx={{ backgroundColor: "#1D1C20" }}
        >
          <InputLabel style={{ color: "white", fontFamily: "Poppins" }}>
            {labelText}
          </InputLabel>
          <Select
            value={selectedOption !== null ? selectedOption : ''}
            label={labelText}
            onChange={handleChange}
            style={{ color: "white" }}
            sx={{
              "& .MuiSvgIcon-root": {
                color: "white",
              },
            }}
          >
            {mappedOption}
          </Select>
        </FormControl>
      </Box>
    </section>
  );
}

export default MySelect;
