import WelcomePanelImg from "../../assets/images/welcome_page_img.svg";
import AuthContext from "../../context/AuthContext";
import { useContext } from "react";
function WelcomePanel() {
  const { userData } = useContext(AuthContext); 

  const greetByYearLevel = (yearLvl) => {
    switch(yearLvl) {
      case 1:
        return 'First Year'
      case 2:
        return 'Second Year'
      case 3:
        return 'Third Year'
      case 4:
        return 'Fourth Year'
      default:
        return 'Year Level did not match the cases';
    }
  }
  return (
    <div className="bg-white flex flex-1 px-6">
      <div className="flex-1 flex items-center justify-center">
        <div>
          <p className="text-5xl font-semibold pb-2">Welcome Back, {userData ? userData.first_name : null}</p>
          <p className="text-xl">You're currently a {greetByYearLevel(userData.year_level)}, Goodluck!</p>
        </div>
      </div>
      <div className="flex-1 flex items-center justify-center py-8">
        <img className="h-[25rem]" src={WelcomePanelImg} alt="" />
      </div>
    </div>
  );
}

export default WelcomePanel;
