import { useState } from "react";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleXmark } from "@fortawesome/free-solid-svg-icons";
import GcashLogo from "../assets/images/gcash-logo.png";
import MasterCardLogo from "../assets/images/xmastercard-logo.png";

import MyButton from "./MyButton";
import { apiUrl } from "../utils/proxy";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 450,
  height: 550,
  bgcolor: "background.paper",
  borderRadius: "5px",
  boxShadow: 24,
  p: 1,
};

function MyModal( {optionList, updateDummyValue, dummyValue} ) {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setMethodOfPayment(null);
    setOpen(false)
  };

  const [methodOfPayment, setMethodOfPayment] = useState(null);

  const [selectedOptionId, setSelectedOptionId] = useState("");

  const handleOptionChange = (event) => {
    setSelectedOptionId(event.target.value);
  };

  const handlePaymentMethodChange = (event) => {
    setMethodOfPayment(event.target.value);
  }

  const updateStudentPayment = async () => {
    const updatedData = {
      status: 'paid'
    };

    const response = await fetch(`${apiUrl}/api/payments/${selectedOptionId}/`, {
      method: 'PATCH',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(updatedData),
    });

    if (!response.ok) {
      throw new Error(`HTTP error! Status: ${response.status}`);
    }

    const data = await response.json();
    updateDummyValue(!dummyValue)
    handleClose();
    console.log('Student updated successfully:', data);

  }


  return (
    <div>
      <MyButton btnText="Pay Now" handleClick={handleOpen} btnWidth='w-full' btnHeight='h-full'/>

      {/* <Button
        sx={{ color: "#fff", width: "100%", textAlign: "center", fontFamily: 'Montserrat', padding: 0 }}
        onClick={}
      >
        
      </Button> */}
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <div className="w-full h-full py-4 px-8 relative">
            <FontAwesomeIcon
              className="text-2xl absolute right-0 top-0 cursor-pointer"
              icon={faCircleXmark}
              style={{ color: "#FFD9D9" }}
              onClick={handleClose}
            />

            <header className="text-center">
              <p className="text-3xl text-myBlue ">Let's Pay Now</p>
              <p>
                Please ensure all informations are correct before proceeding
              </p>
            </header>

            <hr className="mt-2 mb-1" />

            <div className="w-full h-[18%] px-2">
              <section className="w-full h-1/2 flex gap-2 py-3 items-center ">
                <input type="radio" name="radioGroup" id="radioOption1" value='credit' onChange={handlePaymentMethodChange}/>

                <i className="h-full p-2 flex items-center rounded-md">
                  <img className="h-[40px]" src={MasterCardLogo} alt="GcashLogo" />
                </i>
              </section>

              <section className="w-full h-1/2 flex gap-2 py-3 items-center">
                <input type="radio" name="radioGroup" id="radioOption2" value='gcash' onChange={handlePaymentMethodChange}/>

                <i className="h-full p-2 flex items-center justify-center rounded-md">
                  <img className="h-[20px]" src={GcashLogo} alt="GcashLogo" />
                </i>
              </section>
            </div>

            {!methodOfPayment && (
              <div className="w-full h-[35%] flex items-center justify-center">
                <p>Choose Method of Payment First</p>
              </div>
            )}

            {methodOfPayment === 'credit' && (
              <div className="w-full h-[35%] ">
                <p className="mb-1">Enter Account Information</p>
                <section className="flex flex-col gap-3">
                  <input
                    className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    type="text"
                    placeholder="Account Name"
                  />
                  <input
                    className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    type="text"
                    placeholder="Mobile No."
                  />
                  <section className="flex justify-between">
                    <input
                      className="shadow appearance-none border rounded w-[47%] py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                      type="text"
                      placeholder="CCV"
                    />
                    <input
                      className="shadow appearance-none border rounded w-[47%] py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                      type="text"
                      placeholder="PIN"
                    />
                  </section>
                  
                </section>
              </div>
            )}

            {methodOfPayment === 'gcash' && (
              <div className="w-full h-[35%] ">
                <p className="mb-1">Enter Account Information</p>
                <section className="flex flex-col gap-3">
                  <input
                    className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    type="text"
                    placeholder="Account Name"
                  />
                  <input
                    className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                    type="text"
                    placeholder="Mobile No."
                  />
                </section>
              </div>
            )}
            
            

            <footer className="w-full h-[24%] mt-3">
              <p className="mb-1">Choose What to Pay</p>
              <section className="flex flex-col gap-3">
                <select
                  className="shadow bg-white border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                  id="combo-box"
                  value={selectedOptionId}
                  onChange={handleOptionChange}
                >
                  <option value="" disable="true" hidden>Choose an option</option>
                  {optionList}
              
                </select>

                <MyButton btnText={"Proceed"} btnHeight={"h-[43px]"} handleClick={updateStudentPayment}/>
              </section>
            </footer>
          </div>
        </Box>
      </Modal>
    </div>
  );
}

export default MyModal;
