import { useState, useEffect, useContext } from "react";
import { apiUrl } from "../../utils/proxy";
import * as React from "react";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import TabContext from "@mui/lab/TabContext";
import TabList from "@mui/lab/TabList";
import TabPanel from "@mui/lab/TabPanel";
import AuthContext from "../../context/AuthContext";
function GradesPanel() {
  const [enrolledSubjects, setEnrolledSubjects] = useState(null);
  const { userData } = useContext(AuthContext);
  useEffect(() => {
    getEnrolledSubjects();
  }, []);

  const getEnrolledSubjects = async () => {
    try {
      const response = await fetch(`${apiUrl}/api/get_grades/${userData.id}/`);
      const data = await response.json();
      setEnrolledSubjects(data);
    } catch (err) {
      console.error("Error Fetching Enrolled Subjects Data: ", err);
    }
  };

  const gradeCategory = ["All", "Graded"];

  const mappedCategory = gradeCategory.map((value, index) => (
    <Tab
      sx={{
        color: "black",
        fontFamily: "Montserrat",
        fontSize: "14px",
        fontWeight: "regular",
      }}
      label={value}
      value={(index + 1).toString()}
    />
  ));

  let subjectList = null;
  let gradeList = null;
  let remarkList = null;
  let feedbackList = null;

  let filteredSubjectList = null;
  let filteredGradeList = null;
  let filteredRemarkList = null;
  let filteredFeedbackList = null;

  const [value, setValue] = React.useState("1");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  if (enrolledSubjects !== null) {
    // All
    subjectList = enrolledSubjects.map((object) => (
      <span className="w-full text-center py-2" key={object.id}>
        {object.subject.code}
      </span>
    ));
    gradeList = enrolledSubjects.map((object) => (
      <span className="w-full text-center py-2" key={object.id}>
        {object.grade ? object.grade : "N/A"}
      </span>
    ));
    remarkList = enrolledSubjects.map((object) => (
      <span className="w-full text-center py-2" key={object.id}>
        {object.remark ? object.remark.toUpperCase() : "N/A"}
      </span>
    ));
    feedbackList = enrolledSubjects.map((object) => (
      <span className="w-full text-center py-2" key={object.id}>
        {object.feedback ? object.feedback : "-----"}
      </span>
    ));
    // Graded
    filteredSubjectList = enrolledSubjects.map((object) =>
      object.grade ? (
        <span className="w-full text-center py-2" key={object.id}>
          {object.subject.code}
        </span>
      ) : null
    );
    filteredGradeList = enrolledSubjects.map((object) =>
      object.grade ? (
        <span className="w-full text-center py-2" key={object.id}>
          {object.grade ? object.grade : "N/A"}
        </span>
      ) : null
    );

    filteredRemarkList = enrolledSubjects.map((object) =>
      object.grade ? (
        <span className="w-full text-center py-2" key={object.id}>
          {object.remark ? object.remark.toUpperCase() : "N/A"}
        </span>
      ) : null
    );

    filteredFeedbackList = enrolledSubjects.map((object) =>
      object.grade ? (
        <span className="w-full text-center py-2" key={object.id}>
          {object.feedback ? object.feedback : "-----"}
        </span>
      ) : null
    );
  }

  if (enrolledSubjects === null) {
    return (
      <div className="bg-white flex flex-col flex-1 px-4 overflow-auto justify-center items-center">
        <p className="text-7xl">Fetching Data...</p>
      </div>
    );
  }

  return (
    <div className="bg-white flex flex-1 px-4 flex-col">
      <Box sx={{ width: "100%", typography: "body1" }}>
        <TabContext value={value}>
          <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
            <TabList onChange={handleChange} aria-label="lab API tabs example">
              {mappedCategory}
            </TabList>
          </Box>
          <section className="flex-1 pt-8 pb-4 flex flex-col">
            <TabPanel value="1">
              <header className="h-fit w-full flex justify-between mb-4 font-medium min-w-[460px] text-xl">
                <span className="text-center w-[30%]">Subject</span>
                <span className="text-center w-[20%] ">Grade</span>
                <span className="text-center w-[20%] ">Remarks</span>
                <span className="text-center w-[30%]">Feedback</span>
              </header>
              <article className="w-full flex-1 flex justify-between min-w-[360px]">
                <div className="flex flex-col w-[30%]">{subjectList}</div>
                <div className="flex-none flex flex-col w-[20%] ">
                  {gradeList}
                </div>
                <div className="flex-none flex flex-col w-[20%] ">
                  {remarkList}
                </div>
                <div className="flex-1 flex flex-col w-[30%]">
                  {feedbackList}
                </div>
              </article>
            </TabPanel>
            <TabPanel value="2">
            <header className="h-fit w-full flex justify-between mb-4 font-medium min-w-[460px] text-xl">
              <span className="text-center w-[30%]">Subject</span>
              <span className="text-center w-[20%] ">Grade</span>
              <span className="text-center w-[20%] ">Remarks</span>
              <span className="text-center w-[30%]">Feedback</span>
            </header>
            <article className="w-full flex-1 flex justify-between min-w-[360px]">
              <div className="flex flex-col w-[30%]">{filteredSubjectList}</div>
              <div className="flex-none flex flex-col w-[20%] ">
                {filteredGradeList}
              </div>
              <div className="flex-none flex flex-col w-[20%] ">
                {filteredRemarkList}
              </div>
              <div className="flex-1 flex flex-col w-[30%]">
                {filteredFeedbackList}
              </div>
            </article>
          </TabPanel>
          </section>

          
        </TabContext>
      </Box>
    </div>
  );
}

export default GradesPanel;
