import creditCardLogo from "../../assets/images/creditCard.svg";
import { useState, useEffect, useContext } from "react";
import { apiUrl } from "../../utils/proxy";
import MyModal from "../MyModal";
import AuthContext from "../../context/AuthContext";
function PayFeesPanel() {

  const { userData } = useContext(AuthContext)
  const [ studentPayments, setStudentPayments ] = useState(null);
  const [ dummyState, setDummyState ] = useState(true);

  useEffect(() => {
      getStudentPayments();
  }, [dummyState]);
  const getStudentPayments = async () => {
    const response = await fetch(`${apiUrl}/api/get_payments/${userData.id}/`);
    const data = await response.json();
    setStudentPayments(data);
  }

  const getFullDate = (dateString) => {
    const dateObject = new Date(dateString);
    const year = dateObject.getFullYear();
    const month = dateObject.getMonth() + 1;
    const day = dateObject.getDate();
    return `${year}-${month}-${day}`;
  } 

  const getTotalUnPaid = (payments, getTotalAmount=false) => {
    let totalUnpaid = 0;
    let totalUnpaidAmount = 0;
    payments.forEach((payment) => {
      if (payment.status === "unpaid"){
        totalUnpaid++;
        totalUnpaidAmount += parseInt(payment.pay_fee.amount);
      }
    });
    if (getTotalAmount) 
      return totalUnpaidAmount;
    return totalUnpaid;
  };

  const getTotalPaid = (payments, getTotalAmount=false) => {
    let totalPaid = 0;
    let totalPaidAmount = 0;
    payments.forEach((payment) => {
      if (payment.status === "paid") {
        totalPaid++;
        totalPaidAmount += parseInt(payment.pay_fee.amount);
      }
    });
    if (getTotalAmount) 
      return totalPaidAmount;
    return totalPaid;
  };

  let dateAssignedList = null;
  let descriptionList = null;
  let amountList = null;
  let statusList = null;

  let paymentSelectionList = null;

  if (studentPayments !== null) {
    dateAssignedList = studentPayments.map((payment) => (
      <span key={payment.id}>{getFullDate(payment.date_assigned)}</span>
    ));
    descriptionList = studentPayments.map((payment) => (
      <span key={payment.id}>{payment.pay_fee.description}</span>
    ));
    amountList = studentPayments.map((payment) => (
      <span key={payment.id}>{`₱ ${payment.pay_fee.amount} PHP`}</span>
    ));
    statusList = studentPayments.map((payment) => (
      <span key={payment.id}>
        {payment.status.charAt(0).toUpperCase() + payment.status.slice(1)}
      </span>
    ));

    paymentSelectionList = studentPayments.map((payment) => (
      payment.status.toLowerCase() === 'unpaid' ? (
      <option key={payment.id} value={payment.id}>{payment.pay_fee.description}</option>
      ) : null
    ));

  }

  
    
  if (studentPayments === null) {
    return (
      <div className="bg-white flex flex-col flex-1 px-4 overflow-auto justify-center items-center">
        <p className="text-7xl">Fetching Data...</p>
      </div>
    )
  }
  return (
    <div className="h-[20rem] bg-transparent flex flex-col flex-1 gap-3">

      <div className="w-full py-7 h-80 flex-initial bg-white flex justify-center">
        <section className="max-w-xs p-6 flex items-center mr-32">
          <img
            className="min-w-[14rem]"
            src={creditCardLogo}
            alt="Credit Card"
          />
        </section>

        <section className="flex flex-col justify-center gap-5">
          <div className="flex">
            <section className="flex items-center w-72 ">
              <p className="text-7xl text-red-500 pr-4">{getTotalUnPaid(studentPayments)}</p>
              <p className="whitespace-break-spaces w-6 leading-5">
                Pending Payment
              </p>
            </section>
            <section className="w-72 flex flex-col justify-center">
              <p className="text-slate-400">Total</p>
              <p>'₱ {getTotalUnPaid(studentPayments, true)}.00 PHP'</p>
            </section>
          </div>

          <div className="flex">
            <section className="flex items-center w-72 ">
              <p className="text-7xl text-green-500 pr-4">{getTotalPaid(studentPayments)}</p>
              <p className="whitespace-break-spaces w-6 leading-5">
                Successful Payment
              </p>
            </section>

            <section className="w-72 flex flex-col justify-center">
              <p className="text-slate-400">Total</p>
              <p>₱ {getTotalPaid(studentPayments, true)}.00 PHP</p>
            </section>
          </div>

          <div className="w-[24.5rem]">
            <MyModal optionList={paymentSelectionList} updateDummyValue={setDummyState} dummyValue={dummyState}/>
          </div>
        </section>
      </div>

      <div className="w-full flex-1 bg-white">
        <section className="w-full h-14 flex flex-col items-center text-xl px-8">

        {/* ###Table Header */}
          <header className="h-fit w-full flex justify-between mt-2 mb-3">
            <span className="text-center flex-initial w-[18rem]">
              Date Assigned
            </span>
            <span className="text-center flex-1">Description</span>
            <span className="text-center flex-initial w-[10rem]">Amount</span>
            <span className="text-center flex-1">Status</span>
          </header>
        {/* Table Header### */}

          <hr className=" w-full mb-4" />

        {/* ###Table Content */}
          <article className="h-fit w-full flex justify-between ">
            <span className="text-center flex-initial w-[18rem] flex flex-col gap-3">
              {dateAssignedList}
            </span>
            <span className="text-center flex-1 flex flex-col gap-3">
              {descriptionList}
            </span>
            <span className="text-center flex-initial w-[10rem] flex flex-col gap-3">
              {amountList}
            </span>
            <span className="text-center flex-1 flex flex-col gap-3 ">
              {statusList}
            </span>
          </article>
        {/* Table Content### */}
         
        </section>
      </div>
    </div>
  );
}

export default PayFeesPanel;
