import { useState, useEffect, useContext } from "react";
import { apiUrl } from "../../utils/proxy";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import TabContext from "@mui/lab/TabContext";
import TabList from "@mui/lab/TabList";
import TabPanel from "@mui/lab/TabPanel";
import AuthContext from "../../context/AuthContext";
function SubjectPanel() {
  const [enrolledSubjects, setEnrolledSubjects] = useState(null);
  const { userData } = useContext(AuthContext);
  const [value, setValue] = useState("1");

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  useEffect(() => {
    getEnrolledSubjects();
  }, []);

  const getEnrolledSubjects = async () => {
    try {
      const response = await fetch(`${apiUrl}/api/get_grades/${userData.id}/`);
      const data = await response.json();
      setEnrolledSubjects(data);
    } catch (err) {
      console.error("Error Fetching Enrolled Subjects Data: ", err);
    }
  };

  const subjectCategory = ["My Subject", "Previous Subject"];

  let mappedSubjects = null;

  if (enrolledSubjects !== null) {
    mappedSubjects = enrolledSubjects.map((object) => {
      let subjectColor;
      let randomNumber = Math.floor(Math.random() * 4);
      let itSubjectColors = ["bg-yellow-300", "bg-orange-300", "bg-yellow-400", "bg-orange-400"]
      let crimtSubjectColors = ["bg-slate-300", "bg-slate-500", "bg-slate-400", "bg-slate-600"]

      switch (object.subject.code.split(" ")[0]) {
        case "IT":
          subjectColor = itSubjectColors[randomNumber]
          break;
        case "PE":
          subjectColor = "bg-blue-300";
          break;
        case "LIT":
          subjectColor = "bg-stone-300";
          break;
        case "FITT":
          subjectColor = "bg-blue-300";
          break;
        case "ENG":
          subjectColor = "bg-blue-100";
          break;
        case "PHIL":
          subjectColor = "bg-slate-400";
          break;
        case "CRIM":
          subjectColor = crimtSubjectColors[randomNumber];
          break;
        default:
          subjectColor = "bg-black"; // Set a default color or handle other cases
      }

      const statusCapitalized =
        object.status.charAt(0).toUpperCase() + object.status.slice(1);

      return (
        <div
          className="relative w-[30.6rem] h-48 flex flex-wra "
          key={object.subject.id}
        >
          <div
            className={
              subjectColor +
              " w-48 h-48 flex items-center justify-center text-7xl font-medium rounded-md"
            }
          >
            {object.subject.code.slice(-2)}
          </div>
          <div className="flex-1 flex flex-col p-4">
            <div className="h-10 w-full mb-8 font-medium text-xl">
              {`${object.subject.code}: ${object.subject.name}`}
            </div>
            <span className="leading-4 font-thin text-sm">
              {object.subject.description}
            </span>
            <span className="absolute bottom-0 bg-slate-300 w-fit py-[2px] px-3 flex items-center justify-center rounded-2xl">
              <div className="text-slate-500">{statusCapitalized}</div>
            </span>
          </div>
        </div>
      );
    });
  }

  const [activeComponent, setActiveComponent] = useState("My Subject");

  const handleActiveComponent = (componentName) => {
    setActiveComponent(componentName);
  };

  const mappedCategory = subjectCategory.map((value, index) => (
    <Tab
      sx={{ color: "black", fontFamily: "Montserrat", fontSize: '14px', fontWeight: 'regular' }}
      label={value}
      value={(index + 1).toString()}
    />
  ));

  if (enrolledSubjects === null) {
    return (
      <div className="bg-white flex flex-col flex-1 px-4 overflow-auto justify-center items-center">
        <p className="text-7xl">Fetching Data...</p>
      </div>
    );
  }

  return (
    <div className="bg-white flex flex-col flex-1 px-4 overflow-auto">
      <Box sx={{ width: "100%", typography: "body1" }}>
        <TabContext value={value}>
          <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
            <TabList onChange={handleChange} aria-label="lab API tabs example">
              {mappedCategory}
            </TabList>
          </Box>
          <TabPanel value="1">
            <section className="flex-1 pt-8 pb-4 flex gap-4 flex-wrap">
              {mappedSubjects}
            </section>
          </TabPanel>
          <TabPanel value="2">
            <section className="flex-1 pt-8 pb-4 flex gap-4 items-center justify-center text-4xl">
              <h1>No Previous Subject.</h1>
            </section>
          </TabPanel>
        </TabContext>
      </Box>
    </div>
  );
}

export default SubjectPanel;
