import SignInLogo from "../assets/images/undraw_online_information_re_erks.svg";
import Form from "../components/Form";
import InputFieldRegister from "../components/InputFieldRegister";
import { useState } from "react";
import InputFieldLogin from "../components/InputFieldLogin";
import myLogo from "../assets/images/infortal-logo-white-cropped.png"
function LoginPage() {
  
  const [haveAccount, setHaveAccount] = useState(true);

  const handleAccount = () => {
    setHaveAccount((haveAccount) => !haveAccount);
  };

  return (
    <main className="h-screen w-full flex">

      {/* ---------------Input Section--------------- */}
      <section className="w-1/2 bg-myDarkColor flex flex-col justify-center text-white px-12 relative">
        {/* #Logo*/}
        <header className="py-6 absolute w-full top-0">
          <img className="w-44" src={myLogo} alt="" />
        </header>

        {haveAccount ? (
          //Render login if have account
          <Form
            headerText="Sign In"
            inputFieldComponent={<InputFieldLogin handleAccount={handleAccount}/>}
          />
        ) : (
          //Render signup if not
          <Form
            headerText="Sign Up"
            inputFieldComponent={
              <InputFieldRegister handleAccount={handleAccount} />
            }
          />
        )}
      </section>

      {/* -------------------SECTION TWO------------------- */}
      <section className="w-1/2 flex items-center justify-center">
        <img className="h-96" src={SignInLogo} alt="" />
      </section>
    </main>
  );
}

export default LoginPage;
