import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import { createTheme, ThemeProvider } from "@mui/material/styles";

const theme = createTheme({
  typography: {
    fontFamily: ["Poppins", "sans-serif"].join(","),
  },
});
function MyTextField({ labelText, widthSize, paddingTop, dataType, value="", setValue, bgColor, isDisabled=false, textColor="white", placeholderColor="white" }) {

  const handleInputChange = (event) => {
    const switchDataType = (type) => {
      switch(type) {
        case 'int':
          return parseInt(event.target.value);
        default:
          return event.target.value;
      }
    }
    setValue(switchDataType(dataType));
  };

  return (
    <section className={`${widthSize} ${paddingTop}`}>
      <ThemeProvider theme={theme}>
        <Box
          component="form"
          sx={{
            "& > :not(style)": { width: '100%' },
            color: "white",
          }}
          noValidate
          autoComplete="off"
        >
          <TextField
            id="outlined-basic"
            label={labelText}
            value={value}
            onChange={handleInputChange}
            variant="outlined"
            InputLabelProps={{ style: { color: placeholderColor } }}
            inputProps={{ style: { color: textColor } }}
            sx={{ backgroundColor: bgColor }}
            disabled={isDisabled ? true : false}
          />
        </Box>
      </ThemeProvider>
    </section>
  );
}

export default MyTextField;
