import Header from "../components/Header";
import SideMenu from "../components/SideMenu";
import { useRef, useState } from "react";
import HomePanel from "../components/Panel/HomePanel";
import ContentWindow from "../components/ContentWindow";
function HomePage() {
  // Ref assigned to the Sidemenu Component
  const sideMenu = useRef(null);

  // Boolean state for identifiying wheter xetend or not the sidemenu.
  const [isToggled, setIsToggled] = useState(false);

  // Boolean state to identify wether to show or not the submenu.
  const [showSubmenu, setShowSubmenu] = useState(false);
  // selected Component State.
  const [selectedComponent, setSelectedComponent] = useState(<HomePanel />);

  // change the width of the sideMenu depending on the Toggled State
  const toggleMenu = () => {
    setIsToggled((isToggled) => !isToggled);
    if (showSubmenu === true) setShowSubmenu(!showSubmenu);
    sideMenu.current.style.width = isToggled ? "66px" : "280px";
  };

  // set the selected component state. 
  // it requires a component as an argument.
  const handleComponentChange = (component) => {
    setSelectedComponent(component); 
  }
    
  return (
    <main className="h-screen w-full flex">
      <SideMenu
        toggleMenu={toggleMenu}
        showSubmenu={showSubmenu}
        setShowSubmenu={setShowSubmenu}
        sideMenu={sideMenu}
        isToggled={isToggled}
        handleMenuCLick={handleComponentChange}
      />

      <section className="w-full flex flex-col">
        <Header toggleMenu={toggleMenu} />
        <ContentWindow selectedComponent={selectedComponent} />
      </section>
    </main>
  );
}

export default HomePage;
