import React from "react";
import { useState } from "react";
import { apiUrl } from "../utils/proxy.js";
import MyTextField from "./MyTextField";
import MySelect from "./MySelect";
import MyDatePicker from "./MyDatePicker";
import MyPasswordField from "./MyPasswordField";
import MyButton from "./MyButton";

function InputFieldRegister({ handleAccount }) {
  const [currentPage, setCurrentPage] = useState(0);

  const [isLoading, setIsloading] = useState(false);

  const [fname, setFname] = useState("");
  const [mname, setMname] = useState("");
  const [lname, setLname] = useState("");
  const [bday, setBday] = useState("");
  const [sex, setSex] = useState(null);
  const [age, setAge] = useState("");
  const [email, setEmail] = useState("");
  const [pass, setPass] = useState("");
  const [cpass, setCpass] = useState("");
  const [course, setCourse] = useState(null);
  const [yrLvl, setYrLvl] = useState(null);
  const [sem, setSem] = useState(null);

  const student = {
    first_name: "",
    middle_name: "",
    last_name: "",
    sex: "",
    age: "",
    birthday: "",
    email: "",
    password: "",
    course: "",
    year_level: "",
    semester: "",
    id: 99,
  };

  const handleRegister = () => {
    setIsloading(true);
    let formattedDate = "";
    if (bday !== "") {
      formattedDate = bday.format("YYYY-MM-DD");
    }
    const studentInfo = [
      fname,
      mname,
      lname,
      sex,
      age,
      formattedDate,
      email,
      pass,
      course,
      yrLvl,
      sem,
    ];
    setValuesToStudent(studentInfo);
  };

  const setValuesToStudent = (studentInfoArray) => {
    studentInfoArray.forEach((value, index) => {
      const field = Object.keys(student)[index];
      student[field] = value;
    });
    // console.log("student:", student);

    fetch(`${apiUrl}/api/register/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(student),
    })
      .then((response) => {
        if (!response.ok){
          setIsloading(false)
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => {
        setIsloading(false)
        // console.log("POST request successful. Response data:", data);
        handleAccount();
      })
      .catch((error) => {
        setIsloading(false)
        console.error("Error during POST request:", error.message);
      });
  };

  const handleNext = () => {
    if (currentPage === 2) return;
    setCurrentPage((currentPage) => currentPage + 1);
  };

  const handlePrev = () => {
    if (currentPage === 0) return;
    setCurrentPage((currentPage) => currentPage - 1);
  };

  return (
    <section className="flex flex-col w-full ">
      <div className="w-full flex flex-wrap justify-between min-h-[280px] ">
        {currentPage === 0 && (
          <>
            <MyTextField
              labelText={"First Name"}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              type={"str"}
              value={fname}
              setValue={setFname}
              bgColor={"#1D1C20"}
            />
            <MyTextField
              labelText={"Middle Name"}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              type={"str"}
              value={mname}
              setValue={setMname}
              bgColor={"#1D1C20"}
            />

            <MyTextField
              labelText={"Last Name"}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              type={"str"}
              value={lname}
              setValue={setLname}
              bgColor={"#1D1C20"}
            />

            <MyDatePicker
              labelText={"Birthday"}
              widthSize={"w-[45%]"}
              value={bday}
              setValue={setBday}
            />

            <MySelect
              labelText={"Sex"}
              optionArray={[
                ["male", "Male"],
                ["female", "Female"],
              ]}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              selectedOption={sex}
              setSelectedOption={setSex}
            />
            <MyTextField
              labelText={"Age"}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              type={"int"}
              value={age}
              setValue={setAge}
              bgColor={"#1D1C20"}
            />
          </>
        )}

        {currentPage === 1 && (
          <>
            <MyTextField
              labelText={"Email"}
              widthSize={"w-full"}
              paddingTop={"pt-[8px]"}
              type={"str"}
              value={email}
              setValue={setEmail}
              bgColor={"#1D1C20"}
            />
            <MyPasswordField
              labelText={"Password"}
              widthSize={"w-[45%]"}
              value={pass}
              setValue={setPass}
            />
            <MyPasswordField
              labelText={"Confirm Password"}
              widthSize={"w-[45%]"}
              value={cpass}
              setValue={setCpass}
            />
            <MySelect
              labelText={"Course"}
              optionArray={[
                [2, "Bachelor of Science in Information Technology"],
                [1, "Bachelor of Science in Criminology"],
              ]}
              widthSize={"w-full"}
              selectedOption={course}
              setSelectedOption={setCourse}
            />
          </>
        )}

        {currentPage === 2 && (
          <>
            <MySelect
              labelText={"Year Level"}
              optionArray={[
                [1, "First Year"],
                [2, "Second Year"],
                [3, "Third Year"],
                [4, "Fourth Year"],
              ]}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              selectedOption={yrLvl}
              setSelectedOption={setYrLvl}
            />
            <MySelect
              labelText={"Semester"}
              optionArray={[
                [1, "First Semester"],
                [2, "Second Semester"],
              ]}
              widthSize={"w-[45%]"}
              paddingTop={"pt-[8px]"}
              selectedOption={sem}
              setSelectedOption={setSem}
            />

            <div className="w-full flex justify-center">
              <MyButton
                btnText={"Register"}
                btnHeight={"h-[40px]"}
                btnWidth={"w-[120px]"}
                handleClick={handleRegister}
                isLoading={isLoading}
              />
            </div>
          </>
        )}
      </div>

      {/* ---------------BUTTON SECTION--------------- */}
      <div className="flex justify-between">
        <MyButton
          btnText={"Back"}
          btnHeight={"h-12"}
          btnWidth={"w-[20%]"}
          handleClick={handlePrev}
        />
        <MyButton
          btnText={"Next"}
          btnHeight={"h-12"}
          btnWidth={"w-[20%]"}
          handleClick={handleNext}
        />
      </div>

      <p className="mt-6 flex justify-center">
        Already have an account? &nbsp;
        <span
          className="cursor-pointer hover:underline"
          onClick={handleAccount}
        >
          Sign up
        </span>
        .
      </p>
    </section>
  );
}

export default InputFieldRegister;
